<template>
  <div class="branch">
    <div>
      <div class="company">
        <div class="left" :class="{ setWdith: hideAddress }" @click="showInfoWindow">
          <!-- 签到点名称 -->
          <div style="font-weight: bold; margin: 10px 0px" v-if="hideAddress">
            {{ branch.name }}
          </div>
          <!-- 签到点地址 -->
          <div class="lighter" :class="{ address: hideAddress }">
            地址：&nbsp;{{ branch.address }}
          </div>
          <!-- 离当前定位点距离 -->
          <div>直线距离&nbsp;{{ distance }}&nbsp;米</div>
        </div>
        <div class="right">
          <!-- <slot> </slot> -->
          <!-- 按钮组 -->
          <img
            src="../assets/signNone.png"
            alt="不能签到"
            v-if="parseFloat(distance) > parseFloat(branch.radius) || parseFloat(distance) == 0"
            class="signImg"
            @click="signFunction(false)"
          />
          <img
            src="../assets/signDone.png"
            alt="可以签到"
            v-else
            class="signImg"
            @click="signFunction(true)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "branch",
  components: {},
  data() {
    return {
      //      currentIndex: 0
    };
  },
  watch: {},
  props: {
    branch: {
      type: Object,
      default() {
        return {};
      },
    },
    hideAddress: {
      type: Boolean,
      default: true,
    },
    distance: {
      type: String,
      default: "0",
    },
    currentIndex: {
      type: Number,
      default: -1
    }
  },
  methods: {
    signFunction(bool) {
      // TODO 发送签到请求
      console.log('点击了签到按钮');
      this.$emit('togglePopupState',bool);//select事件触发后，自动触发showCityName事件
    },
    showInfoWindow(){
      this.$emit("branchClick",this.currentIndex)
    }
  },
  created(){
  }
};
</script>

<style scoped>
.branch {
  width: 95%;
  margin: 0 auto;
}
.company,
.staff {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f5eded;
  margin: 10px auto;
  padding-bottom: 10px;
}
.setWdith {
  width: 72%;
}
.company .left div {
  font-size: 14px;
  text-align: left;
}
.company .right {
  text-align: center;
}
.company .right span {
  border: 0.5px solid;
  color: #4886fe;
  font-size: 10px;
  height: 12px;
  line-height: 12px;
  padding: 0 5px;
}

.lighter {
  font-size: 12px;
  color: #999;
}
.staff a {
  font-size: 12px;
  color: #28a745;
  text-decoration: none;
}
.staff a > i {
  padding-left: 0.5em;
}
.line {
  border-bottom: 1px solid #f5eded;
  margin: 10px 0;
}
.address {
  overflow: hidden;
  margin: 5px 0px;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.signImg {
  width: 44px;
  height: 44px;
}
</style>