<template>
  <!-- this.myPosition -->
  <!-- 目前使用袁老师的AK -->
  <baidu-map
    @ready="handler"
    class="map"
    :zoom="zoom"
    :min-zoom="13"
    :center="{ lng: 120.216974, lat: 30.248059 }"
    ref="baiduMap"
    ak="TpyIBVQAG5YyXj3IdbUVAM4ttar4sURe"
  >
    <bm-navigation anchor="BMAP_ANCHOR_TOP_RIGHT"></bm-navigation>
    <!-- <bm-geolocation anchor="BMAP_ANCHOR_TOP_LEFT" :showAddressBar="true"></bm-geolocation> -->
    <!-- 绘制签到区域 -->
    <template v-for="(item, index) in signData">
      <!-- 绘制签到范围 -->
      <bm-circle
        :key="String(index) + '-circle'"
        :center="item.point"
        :radius="item.radius"
        stroke-color="blue"
        :stroke-opacity="0.33"
        :stroke-weight="1"
      ></bm-circle>
      <!-- 绘制锚点 -->
      <bm-marker
        :key="String(index) + '-marker'"
        :position="item.point"
        :dragging="false"
        animation="BMAP_ANIMATION_BOUNCE"
        @click="infoWindowOpen(index, item)"
      >
        <bm-info-window
          :show="currentIndex === index"
          @close="infoWindowClose"
          :width="250"
          :title="item.name"
          :key="index"
          class="bmwindow"
        >
          <!-- <div :class="{'wrapper': branch.workers.length > 3 }" ref='branch'>
          <branch :branch='branch'></branch>
        </div> -->
          <div class="direct">
            <input
              type="button"
              class="btn"
              value="到这里去"
              @click="gotoMap(item.point)"
            />
          </div>
        </bm-info-window>
      </bm-marker>
    </template>
  </baidu-map>
</template>


<script type="text/javascript" src="http://api.map.baidu.com/api?v=2.0&ak=TpyIBVQAG5YyXj3IdbUVAM4ttar4sURe"></script>
<script type="text/javascript" src="http://developer.baidu.com/map/jsdemo/demo/convertor.js"></script>

<script>
import { mapState, mapMutations } from "vuex";
import { SET_CITY, SET_POSITION } from "@/store/mutation_types";

import BaiduMap from "vue-baidu-map/components/map/Map.vue";
import BmNavigation from "vue-baidu-map/components/controls/Navigation";
import BmGeolocation from "vue-baidu-map/components/controls/Geolocation";
import BmMarker from "vue-baidu-map/components/overlays/Marker";
import BmCircle from "vue-baidu-map/components/overlays/Circle";
import BmInfoWindow from "vue-baidu-map/components/overlays/InfoWindow";
import Branch from "./Branch";
import BScroll from "better-scroll";

export default {
  name: "branch-map",
  components: {
    BaiduMap,
    BmNavigation,
    BmGeolocation,
    BmMarker,
    BmInfoWindow,
    Branch,
    BmCircle,
  },
  props: {
    branchesList: {
      type: Array,
      default() {
        return [];
      },
    },
    zoom: {
      type: Number,
      default: 15,
    },
    parentIndex: {
      type: Number,
      default: -1,
    },
    signData: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  watch: {},
  created() {},
  data() {
    return {
      map: {},
      myUrl: require("@/assets/myicon.png"),
      BMap: {},
      //      currentIndex: -1,
      curCity: "",
      startMovePosition: {
        lat: 0,
        lng: 0,
      },
      currentIndex: -1, // 默认值为-1 默认不显示
    };
  },
  computed: {
    ...mapState(["myPosition", "myCity"]),
    // currentIndex: {
    //   get() {
    //     return this.parentIndex;
    //   },
    //   set(v) {},
    // },
  },
  methods: {
    ...mapMutations({
      setCity: SET_CITY,
      setPosition: SET_POSITION,
    }),
    savePosition(lng, lat) {
      this.setPosition({ lng, lat });
      this.startMovePosition = { lng, lat };
    },
    handler({ BMap, map }) {
      // // 强制获取一次定位
      // let geoControl = new BMap.GeolocationControl();
      // geoControl.location();
      // //监听定位成功事件
      // geoControl.addEventListener("locationSuccess", function (e) {
      //   alert("success");
      // });
      // //监听定位失败事件
      // geoControl.addEventListener("locationError", function (e) {
      //   alert("error");
      // });

      this.setPosition({});
      // 强制获取一次地理位置
      useNativeGetCurrentPosition()
        .then(() => {
          // 定位
          this.map = map;
          this.BMap = BMap;
          // 获取定位
          let geolocation = new BMap.Geolocation();
          const _this = this;
          geolocation.getCurrentPosition(function (r) {
            if (this.getStatus() === BMAP_STATUS_SUCCESS) {
              // 保存当前定位
              _this.savePosition(r.point.lng, r.point.lat);
              // alert(`${r.point.lng},${r.point.lat}`);
              let myIcon = new BMap.Icon(_this.myUrl, new BMap.Size(32, 32));
              // 绘制当前位置图标
              let mk = new BMap.Marker(r.point);
              mk.setIcon(myIcon);
              map.addOverlay(mk);
              // 移动到定位点
              map.panTo(r.point);
              console.log(r.point);
              mk.setAnimation(BMAP_ANIMATION_BOUNCE);
              // 修改父组件的变量
              _this.$parent.mapState = true;
            } else {
              alert(
                `获取当前位置信息失败：${this.getStatus()}\n请重新进入本页面`
              );
              console.log("failed ", this.getStatus());
            }
          });
        })
        .catch(() => {
          // alert("获取定位失败");
        });
    },
    async reloadMap() {
      this.$parent.mapState = false;
      this.$parent.mapLoading = true;
      setTimeout(() => {
        this.$parent.mapState = true;
        this.$parent.mapLoading = false;
      }, 1000);
    },
    changeCenter(index, item) {
      console.log("==changecenter=====" + index, item);
      this.infoWindowOpen(index, item);
      this.map.panTo(new BMap.Point(item.point.lng, item.point.lat));
    },
    infoWindowOpen(index, item) {
      //这里还存在问题，如果不加close，则重复点击同一个标记，由于数据没有变化，不会重新更新画面
      console.log(
        "info window is open:" + index + "\ncurrentIndex" + this.currentIndex
      );
      if (this.currentIndex === index) {
        this.currentIndex = -1;
        // this.$nextTick(() => {
        //   //解决方案
        //   this.currentIndex = index;
        //   this.$emit("updateIndex", index);
        // });
      } else {
        this.currentIndex = index;
        // this.$emit("updateIndex", index);
      }

      /*       this.$nextTick(()=>{//想弄一个滚动，不成功，换成原生的滚动条成功
        if(this.branchesList[index].workers.length > 5){
          console.log(document.querySelector('.wrapper'))
          this.scroll = new BScroll(document.querySelector('.wrapper'), {
            probeType: 1,
            click: true,
          })
          console.log(this.scroll)
          this.scroll.refresh()
        }
      }) */
    },
    infoWindowClose(e) {
      //infowwindowclose在open之前调用，所以当有一个窗口在显示时，如果点击窗口，则窗口被此事件关闭了
      //     this.currentIndex = -1
      console.log(e);
      console.log("=======info window is closed");
    },
    gotoMap(destination) {
      console.log(destination);
      //经纬度如果不准的话将导航不出来，此时可以通过中文导航，如果通过中文，还有一个region一定要输入，否则不知道具体地方
      //可先通过经纬度试一下，如果不行再换名称
      let href =
        "http://api.map.baidu.com/direction?origin=latlng:" +
        this.myPosition.lat +
        "," +
        this.myPosition.lng +
        "|name:我的位置" +
        "&destination=" +
        destination.lat +
        "," +
        destination.lng +
        "&mode=driving&region=" +
        '杭州' +
        "&&output=html&src=tlzxzj.com";
      console.log("=====" + href);
      // alert(href)
      window.location.href = href;
      //window.location.href = 'http://api.map.baidu.com/direction?origin=latlng:29.88525897,121.57900597|name:我的位置&destination=建行中山支行&mode=driving&region=杭州&output=html&src=yun.tlzxzj.com'
    },
  },
};

function useNativeGetCurrentPosition() {
  return new Promise((resolve, reject) => {
    // let geolocation = new BMap.Geolocation();
    // const _this = this;
    // geolocation.getCurrentPosition(function (r) {
    //   if (this.getStatus() === BMAP_STATUS_SUCCESS) {
    //     resolve();
    //   } else {
    //     reject();
    //   }
    // });
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          console.log(position);
          // alert(
          //   `In navigator:${position.coords.latitude}/${position.coords.longitude}`
          // );
          resolve();
        },
        (msg) => {
          console.log("定位失败的错误提醒", msg);
          // alert("navigator.geolocation => 定位失败");
          reject();
        }
        // TODO
        ,
        {
          // 指示浏览器获取高精度的位置，默认为false
          enableHighAcuracy: true,
          // 指定获取地理位置的超时时间，默认不限时，单位为毫秒
          // timeout: 36000,
          // 最长有效期，在重复获取地理位置时，此参数指定多久再次获取位置。
          maximumAge: 0,
        }
      ); // 成功方法，失败方法
    } else {
      alert("不支持地理定位。");
    }

    // navigator.geolocation.getCurrentPosition((position) => {
    // let lat = position.coords.latitude;
    // let lng = position.coords.longitude;
    // const pointBak = new BMap.Point(lng, lat);
    // const convertor = new BMap.Convertor();
    // convertor.translate([pointBak], 1, 5, function (resPoint) {
    //   if (resPoint && resPoint.points && resPoint.points.length > 0) {
    //     lng = resPoint.points[0].lng;
    //     lat = resPoint.points[0].lat;
    //   }
    //   const point = new BMap.Point(lng, lat);
    //   const geo = new BMap.Geocoder();
    //   geo.getLocation(point, (res) => {
    //     // console.log(geo, point, res);
    //     console.log("重新读取地理位置中");
    //     resolve();
    //   });
    // });

    // });
  });
}
</script>

<style scoped>
.map {
  width: 100%;
  height: 100%;
}
.direct {
  text-align: center;
}
.wrapper {
  height: 168px;
  overflow: auto;
}
.btn {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #333;
  background-color: #ebebeb;
  border-color: #adadad;
  padding: 5px 10px;
  font-size: 12px;
  line-height: 1.5;
  border-radius: 3px;
}
</style>
