<template>
  <div class="plat">
    <div class="top" id="top">
      <!-- 使用V-if强制刷新地图组件 -->
      <Map :signData="signData" ref="map" v-if="mapState" :parentIndex="currentIndex" >
        <!-- @updateIndex="updateIndex" -->
      </Map>
    </div>
    <!-- 刷新动画 -->
    <transition name="fade">
      <van-button
        icon="replay"
        type="primary"
        :round="true"
        @click="reloadMap()"
        style="z-index: 3; transform: translate(0px, -20px)"
        :loading="mapLoading"
        loading-text="正在刷新"
        >刷新定位</van-button
      >
    </transition>
    <!-- 签到点列表 -->
    <div class="workers">
      <Branch
        v-for="(item, index) in signData"
        :key="index"
        :branch="item"
        :distance="item.distance"
        @togglePopupState="togglePopupState"
        :currentIndex="index"
        @branchClick="branchClick"
      >
      </Branch>
    </div>
    <van-popup
      v-model="showPopup"
      round
      position="center"
      :style="{ height: '30%', width: '50%' }"
      :safe-area-inset-bottom="true"
      :closeable="true"
    >
      <div class="center" v-if="signResult">
        <img
          src="../assets/success.png"
          alt="签到成功"
          style="width: 66%; height: 66%"
        />
        <h3>签到成功</h3>
      </div>
      <div v-else class="center">
        <img
          src="../assets/error.png"
          alt="请到达指定地点后签到"
          style="width: 55%; height: 55%"
        />
        <h3>请到达指定地点后签到</h3>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Map from "@/components/Map";
import Branch from "@/components/Branch";
import { mapState, mapMutations } from "vuex";
import { SET_BRANCHES } from "@/store/mutation_types";
import { district, getDistrictValue } from "@/utils/district";
// 陶宇豪
// import signData from "../mock/signAddress.json";
import { calDistrance } from "@/utils/common";
// 使用本地图标，防止CDN加载失败导致的图标不显示错误
import "vant/lib/index.css";
import "vant/lib/icon/local.css";
import Cookies from "js-cookie";
//
export default {
  name: "sign-address",
  components: {
    Map,
    Branch,
  },
  data() {
    return {
      mapState: true, // 使用v if 快捷刷新Map组件
      showPopup: false, // 显示弹窗
      mapLoading: false, // 刷新按钮状态
      signResult: false, // 签到状态，默认签到错误
      currentIndex: -1, // 显示的详情弹窗index
      center: {}, // 中心点位置
      signData: [
        {
          name: "三桥",
          address: "江干区之江路",
          point: {
            lng: 120.21642,
            lat: 30.239542,
          },
          distance: 0,
          radius: 200,
        },
        {
          name: "省分行",
          address: "省分行",
          point: {
            lng: 120.216974,
            lat: 30.248059,
          },
          distance: 0,
          radius: 50,
        },
      ],
      mapLoadState: false, // 地图加载状态,加载完成后显示签到列表
    };
  },
  computed: {
    ...mapState(["myCity", "myPosition", "nearbyBranches"]),
    getTopDivOffsetHeight: function () {
      return { height: document.getElementById("top").offsetHeight * 0.6 };
    },
  },
  watch: {
    // 监测 myPosition
    myPosition: function (postion) {
      //获取到初始位置则从后台取数据
      this.mapLoadState = true;
      // this.$store.dispatch("getBranchesList");
      // 计算目前位置到签到点的距离
      // 每次地图刷新加载时均会计算
      for (let index = 0; index < this.signData.length; index++) {
        var miles = (
          this.calDistrance(
            // 判断是不是undefined
            this.myPosition.lat == undefined
              ? this.signData[index].point.lat
              : this.myPosition.lat,
            this.myPosition.lng == undefined
              ? this.signData[index].point.lng
              : this.myPosition.lng,
            this.signData[index].point.lat,
            this.signData[index].point.lng
          ) * 1000
        ).toFixed(0);
        this.signData[index].distance = miles;
      }
    },
  },
  created() {},
  methods: {
    ...mapMutations({
      setBranches: SET_BRANCHES,
    }),
    reloadMap() {
      // localStorage.clear();
      this.mapState = false;
      this.$refs.map.reloadMap();
    },
    branchClick(index) {
      console.log("点击了Branch", index);
      if (this.currentIndex === index) {
        //重复点击
        this.currentIndex = -1;
      } else {
        this.currentIndex = index;
        this.center = this.signData[index].point;
        this.$refs.map.changeCenter(index, this.signData[index]);
      }
    },
    updateIndex(index) {
      this.currentIndex = index;
      console.log('updateIndex function success',this.currentIndex,index);
      // document.querySelector('.workers').scrollTop = 48 * index
    },
    toggleMapState() {
      this.mapState = !this.mapState;
    },
    togglePopupState(result) {
      console.log("在外层签到按钮");
      this.showPopup = true;
      this.signResult = result;
    },
  },
};
</script>

